export default {
  setSteps(state, steps) {
    state.steps = steps
  },
  setStep(state, step) {
    state.step = step
  },
  setLocation(state, location) {
    state.location = location
  },
  setFamily(state, family) {
    state.family = family
  },
  setProduct(state, product) {
    state.product = product
  },
  setItem(state, item) {
    state[item.key] = item.value
  }
}